body {
  font-family: 'Poppins', sans-serif;
  background-color: #FAFAFA;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}


header {
  background-image: url(../images/travel-hero.jpg);
  box-shadow: rgba(0, 0, 0, 0.6 );
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  width: 100vw;
  height: 500px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 100px;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* Задайте здесь необходимую прозрачность (от 0 до 1) */
  }
}

h1 {
  color: #FAFAFA;
  font-size: 60px;
  margin: 0;
  z-index: 10;
}

header p {
  color: #FAFAFA;
  font-size: 20px;
  z-index: 10;
}


.nav-container {
  width: 600px;
  border: 1px solid black;
  margin: 50px 10px;
  padding: 30px;
  padding-bottom: 50px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.nav-container h2 {
  font-size: 45px;
  margin: 20px 0;
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.nav-item {
  padding: 20px 10px 30px;
  margin-bottom: 10px;
  border-bottom: 1px solid #706e6e;
}

.nav-item a {
  text-decoration: none;
  color: black;
  line-height: 30px;
  transition: color 250ms cubic-bezier(0.075, 0.82, 0.165, 1);

  &:hover {
    color: #3131ff;
  }
}

.reviews {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 30px;
  padding: 20px;
  margin-bottom: 30px;
}

.review {
  display: flex;
  width: 1000px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  gap: 30px;
}

.review-img {
  padding-top: 50px;
}

.review img {
  // width: 300px;
  margin-top: auto;
  margin-bottom: auto;
  width: 300px;
  height: 400px;
  object-fit: cover;
  border-radius: 10px;
}

.review-info h2 {
  font-size: 40px;
  line-height: 50px;
  margin: 10px 0 35px;
}

.author {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px 0 40px;
 
}

.author h3 {
    font-size: 20px;
    line-height: 100%;
    margin: 0;
}

.author .rating {
  margin: 0;
  color: #706e6e;
}

.review p {
  margin: 35px 0;
  font-size: 20px;
  line-height: 130%;
}



.comments {
  margin-left: auto;
  width: 150px;
  height: 20px;
  display: flex;
  align-items: center;
  gap: 10px;

    & img {
      width: 18px;
      height: 18px;
    }

    & p {
      font-size: 15px;
    }
}

footer {
  width: 100%;
  height: 100px;
  background-color: #706e6e;
  padding: 40px;
}

footer p {
  color: #FAFAFA;
  text-align: center;
  margin: auto;

}